"use client";

import { useState } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { motion } from "framer-motion";

const categories = ["رجالي", "نسائي", "أطفال"];

const products = [
  {
    name: "بنطال جينز رجالي ضيق",
    price: "$49.99",
    image: "/products/mens-jeans.jpg",
    category: "رجالي",
  },
  {
    name: "فستان صيفي نسائي",
    price: "$39.99",
    image: "/products/womens-dress.jpg",
    category: "نسائي",
  },
  {
    name: "تيشيرت أطفال بطبعة",
    price: "$19.99",
    image: "/products/kids-tee.jpg",
    category: "أطفال",
  },
  {
    name: "قميص رجالي كاجوال",
    price: "$34.99",
    image: "/products/mens-shirt.jpg",
    category: "رجالي",
  },
  {
    name: "ليجن يوغا نسائي",
    price: "$29.99",
    image: "/products/womens-leggings.jpg",
    category: "نسائي",
  },
  {
    name: "جاكيت جينز أطفال",
    price: "$39.99",
    image: "/products/kids-jacket.jpg",
    category: "أطفال",
  },
];

export default function NewArrivals() {
  const [selectedCategory, setSelectedCategory] = useState(null);

  const filteredProducts = selectedCategory
    ? products.filter((product) => product.category === selectedCategory)
    : products;

  return (
    <div className="flex items-center justify-center">
      <div
        className="p-4 sm:p-8 bg-[#f1f1f2] min-h-screen w-11/12 sm:w-4/5 max-lg:w-full"
        dir="rtl"
      >
        <h1 className="text-2xl sm:text-3xl font-bold mb-4">وصل حديثًا</h1>

        {/* أزرار الفلترة */}
        <div className="flex flex-wrap gap-2 sm:gap-4 mb-6">
          {categories.map((category) => (
            <Button
              key={category}
              variant="outline"
              className={`rounded-full border-2 text-sm sm:text-base transition-colors duration-300 ${
                selectedCategory === category
                  ? "bg-orange-500 text-white border-orange-500"
                  : "bg-white text-black border-gray-300"
              }`}
              onClick={() =>
                setSelectedCategory(
                  selectedCategory === category ? null : category
                )
              }
            >
              {category}
            </Button>
          ))}
        </div>

        {/* المنتجات */}
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 sm:gap-6">
          {filteredProducts.map((product, index) => (
            <motion.div
              key={index}
              className="flex flex-col gap-2 text-right"
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4, delay: index * 0.05 }}
            >
              <div className="rounded-2xl overflow-hidden shadow-lg bg-white hover:shadow-xl transition-shadow duration-300">
                <Image
                  src={product.image}
                  alt={product.name}
                  width={300}
                  height={300}
                  className="object-cover w-full h-[220px] sm:h-[250px] md:h-[280px]"
                />
              </div>
              <div className="text-sm font-medium text-black truncate">
                {product.name}
              </div>
              <div className="text-sm text-gray-600">{product.price}</div>
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  );
}
